.route-link {
  color: #f4f4f4;
  text-decoration: none; }

.route-link:visited {
  color: #f4f4f4; }

.typeWriter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  height: calc(100vh - 150px);
  background: #131313;
  color: #fff;
  padding: 0 100px; }

.arrow-wrapper {
  display: block;
  margin: 0 auto;
  height: 100px; }

.Typist {
  height: 100px;
  font-family: 'Raleway';
  color: #fff;
  font-size: 20pt; }

.code-logo {
  height: 35px;
  -webkit-animation: show 20s, glow 4s infinite;
          animation: show 20s, glow 4s infinite;
  opacity: 1;
  -webkit-animation-delay: 0s, 20s;
          animation-delay: 0s, 20s; }

@-webkit-keyframes show {
  0% {
    opacity: 0; }
  50% {
    opacity: 0; }
  95% {
    opacity: .75; }
  100% {
    opacity: 1; } }

@keyframes show {
  0% {
    opacity: 0; }
  50% {
    opacity: 0; }
  95% {
    opacity: .75; }
  100% {
    opacity: 1; } }

@-webkit-keyframes glow {
  0% {
    opacity: .50; }
  50% {
    opacity: .75; }
  75% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@keyframes glow {
  0% {
    opacity: .50; }
  50% {
    opacity: .75; }
  75% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@media screen and (max-width: 414px) {
  .typeWriter {
    height: calc(100vh - 100px);
    padding: 100px 15px; }
  .typist {
    font-size: 16pt; } }



